@import "./trivial-index.scss";

div.main {
    padding: 2rem;
    display: inline-block;
    border-left: #eee solid 2px;
}

.main > h3 {
    margin-top: 0;
}

.main > h3 > a {
    color: #000;
    text-decoration: none;
    font-size:24px;
}

.main > h3 > a:link {
    color: #000;
}

.main > h3 > a:visited {
    color: #555;
}

.main > h3 > a:hover {
    color: #337ab7;
}

.main > div > span {
    // font-size: 1.4rem;
    margin-right: 1rem;
}

.main > div > span:before {
    padding-right: 0.5rem;
}

.main .fa-tags > label {
    margin-right: 0.2rem;
}

